  background-color: red;
  border-radius: 1rem;
  padding: .5rem;
}

.image:hover {
  cursor: pointer;
  border: .5rem solid #fff;
  border-radius: 3rem;
  box-shadow: 0 0 20px #4e4e4e;
}

.image:hover .item, .image:hover .title {
  transition: all .5s ease-in;
  transform: translateY(0);
}

.button:hover {
