@media screen and (max-width: 1200px) {
  .dropdown-li {
    display: inline-block;
  }

  nav {
    width: 100vw;
    display: flex;
  }

  .nav-bar {
    justify-content: space-between;
    padding: .5rem 3rem;
    display: flex;
  }

  header ul {
    display: none;
  }
}

@media screen and (max-width: 480px) {
  header img {
    padding-left: 1vw;
  }

  .dropdown-li {
    padding: 1vw;
    display: inline-block;
  }

  .cart {
    padding-left: 1vw;
  }

  nav {
    width: 100vw;
    display: flex;
  }

  .nav-bar {
    justify-content: flex-end;
    padding: 1vw;
    display: flex;
  }

  .search, header ul {
    display: none;
  }
}

@media screen and (max-width: 150px) {
  header {
    flex-direction: column;
    justify-content: center;
    align-items: center;
    padding: 1vw;
    display: flex;
  }
}

